import { createWebHistory, createRouter } from "vue-router";
import ListComp from './components/List.vue';
import HomeComp from './components/Home.vue';
import DetailComp from './components/Detail.vue';
import ErrorComp from './components/Error.vue';
import AuthorComp from './components/Author.vue';
import CommentComp from './components/Comment.vue';

// 순서에따라 라우팅 우선순위가 결정된다.
const routes = [
  {
    path: '/list',
    component: ListComp,
  },
  {
    path: '/',
    component: HomeComp,
  },
  {
    // 파라미터에는 정규식도 입력이 가능함 vue-router 4 로 검색
    // path: '/detail/:id(\\d+)', //숫자만 가능
    // path: '/detail/:id*', //반복 됨 

    path: '/detail/:id',
    component: DetailComp,
    children: [
     {
      path : 'author',
      component : AuthorComp,
     },
     {
      path : 'comment',
      component : CommentComp,
     }
    ]
  },
  {
    //path: '/:anything(.*)', // 모든문자라는 정규식
    path: '/:dajkfldf',
    component: ErrorComp,
  },
];
// const routes = [
//   {
//     path: "/경로",
//     component: import해온 컴포넌트,
//   }
// ];



const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 